'use client'
import React from "react";
import Template1 from "@/components/Template1";
import Template2 from "@/components/Template2";
import dynamic from "next/dynamic";

const Perview = ({ data }) => {
  const previewPayload  = {
  "userId": 1,
  "templateName": "modern",
  "sections": [
    {
      "section": "Header",
      "enabled": true,
      "fields": [
        {
          "name": "logo",
          "label": "Upload Logo",
          "type": "image",
          "required": true
        },
        {
          "name": "title",
          "label": "Site Title / Brand Name",
          "type": "text",
          "required": true
        }
      ]
    },
    {
      "section": "Description",
      "enabled": true,
      "fields": [
        {
          "name": "heading",
          "label": "Heading",
          "type": "text",
          "required": true
        },
        {
          "name": "description",
          "label": "Description Text",
          "type": "textarea",
          "required": true
        }
      ]
    },
    {
      "section": "Specification",
      "enabled": true,
      "fields": [
        {
          "name": "specifications",
          "label": "Product Specification List",
          "type": "repeater",
          "required": false,
          "fields": [
            {
              "name": "label",
              "label": "Specification Title",
              "type": "text",
              "required": true
            },
            {
              "name": "value",
              "label": "Specification Value",
              "type": "text",
              "required": true
            }
          ]
        }
      ]
    },
    {
      "section": "YouTube",
      "enabled": true,
      "fields": [
        {
          "name": "videoUrl",
          "label": "YouTube Video Embed URL",
          "type": "url",
          "required": false,
          "placeholder": "https://www.youtube.com/embed/..."
        }
      ]
    },
    {
      "section": "CTA",
      "enabled": true,
      "fields": [
        {
          "name": "ctaText",
          "label": "Button Text",
          "type": "text",
          "required": true
        },
        {
          "name": "ctaUrl",
          "label": "Button URL",
          "type": "url",
          "required": true
        }
      ]
    },
    {
      "section": "Social Media",
      "enabled": true,
      "fields": [
        {
          "name": "facebook",
          "label": "Facebook URL",
          "type": "url"
        },
        {
          "name": "instagram",
          "label": "Instagram URL",
          "type": "url"
        },
        {
          "name": "youtube",
          "label": "YouTube URL",
          "type": "url"
        },
        {
          "name": "twitter",
          "label": "Twitter URL",
          "type": "url"
        }
      ]
    },
    {
      "section": "Contact",
      "enabled": true,
      "fields": [
        {
          "name": "phone",
          "label": "Phone Number",
          "type": "text",
          "required": true
        },
        {
          "name": "email",
          "label": "Email Address",
          "type": "email",
          "required": true
        },
        {
          "name": "address",
          "label": "Address",
          "type": "textarea",
          "required": false
        },
        {
          "name": "directionButtonText",
          "label": "Direction Button Text",
          "type": "text",
          "required": false,
          "placeholder": "e.g. Get Directions"
        },
        {
          "name": "directionUrl",
          "label": "Direction URL",
          "type": "url",
          "required": false,
          "placeholder": "https://maps.google.com/?q=..."
        }
      ]
    }
  ],
  "styles": {
    "primaryColor": "#ecedf4ff",
    "backgroundColor": "#000000",
    "textColor": "#a97d38",
    "headlineSize": 28,
    "paragraphSize": 16
  }
};

  function getTemplateComponent(type) {
    switch (type) {
      case "modern":
        return Template1;
      case "basic":
        return Template2;
      default:
        return null;
    }
  }

  const ContentComponent = getTemplateComponent(previewPayload.templateName);

  return (
    <div>
      {ContentComponent ? (
        <ContentComponent data={previewPayload} />
      ) : (
        <div>No template selected</div>
      )}
    </div>
  );
};

export default Perview;
